.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

* {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

.box {
  color: white;
  font-size: 100px;
  text-align: center;
  text-shadow: 4px 4px 0 rgba(0,0,0,0.1);
  padding: 10px;
}

.box1{ background-color: #1abc9c}
.box2{ background-color: #3498db}
.box3{ background-color: #9b59b6}
.box4{ background-color: #34495e}
.box5{ background-color: #f1c40f}
.box6{ background-color: #e67e22}
.box7{ background-color: #e74c3c}
.box8{ background-color: #bdc3c7}
.box9{ background-color: #2ecc71}
.box10{ background-color: #16a085}


/*we start writing our flexbox here*/
.container {
  display: flex; 
  border: 10px solid goldenrod;
  min-height: 100vh; 
  flex-wrap: wrap;
}

.box {
  width: 33.33333333%;
}